import { Component, ViewChild, OnInit } from '@angular/core';
import { IonicPage, App, Platform, Events, NavController, Tabs, ViewController, NavParams, LoadingController, AlertController, ModalController } from 'ionic-angular';
import { AppSoundProvider } from '../../providers/app-sound/app-sound';

@IonicPage()
@Component({
  selector: 'page-offer-buyresultpage',
  templateUrl: 'offer-buyresultpage.html',
})
export class OfferBuyresultpagePage {
  syndicateName: any;
  status: boolean;
  public nav: NavController;
  public homeOffer: boolean = false;
  constructor(
    private navParams: NavParams,
    public events: Events,
    private viewCtrl: ViewController, public appSound: AppSoundProvider,
    private navCtrl: NavController,
    public app: App) {
    this.homeOffer = this.navParams.get("homeCard")

    this.syndicateName = this.navParams.get("syndicateName").syndicate_name;
    if (!this.syndicateName) {
      this.syndicateName = "Hello"
    }
    this.status = this.navParams.get("status");
    this.nav = this.app.getRootNav();
  }
  getmoreline() {
    this.appSound.play('buttonClick');
    var data = { "data": "getMoreLines" }
    this.viewCtrl.dismiss(data);
  }
  moveToSyndicate(data: string = "move") {
    this.events.publish("reloadSyndicate")
    this.appSound.play('buttonClick');
    this.viewCtrl.dismiss(data);
  }
  tryAgain() {
    this.appSound.play('buttonClick');
    this.viewCtrl.dismiss();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad OfferBuyresultpagePage');
  }

}



